import { cloneElement, isValidElement, ReactNode } from 'react';
import { absorbProps } from './mergeProps';

export const cloneReactNode = (node: ReactNode, props: Record<string, any>) => {

  if (isValidElement(node)) {
    return cloneElement(node, absorbProps(props, node.props));
  } else {
    return node;
  }
}